.dettaglio-costi-container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 30px;
  align-items: center;
  .section {
    .input-container {
      margin-top: 20px;
      max-width: 200px;
    }
    &.date {
      display: grid;
      grid-template-columns: 1fr 1fr;
      .date-container {
      }
    }
  }
}
